// @flow
import React, { Fragment, useState } from 'react';
import _isEmpty from 'lodash/isEmpty';
import _get from 'lodash/get';
import { Loader } from 'react-loaders';
import BlockUi from 'react-block-ui';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import type { Element } from 'react';
import { withErrorBoundary } from 'components/ErrorBoundary';
import Offer from './Offer';
import { mappingCounterData } from 'helpers/Offer';
import type { Offer as OfferType } from 'types/Offer';
import ConfirmTransferModal from 'components/BuyerOfferStep/Step500/ConfirmTransferModal';
import ConfirmInspectionDialog from 'components/BuyerOfferStep/Step600/ConfirmInspectionDialog';
import OriginalOfferDialog from 'components/BuyerOffersSteps/OriginalOfferDialog';
import CreateEndpointAccount from 'components/BuyerOfferStep/Step350/CreateEndpointAccount';
import {
  hideWireConfirmButton,
  hideCreateEndPointAccountConfirmButton,
} from './utils/helpers';

type Props = {
  offers: Array<OfferType>,
  getBuyerOffers: Function,
  isLoading: boolean,
};

const useStyles = makeStyles(theme => ({
  container: {
    minHeight: '100px',
    marginBottom: theme.spacing(3),
  },
  loading: {
    border: '1px solid #E0E0E0',
    backgroundColor: theme.palette.common.white,
  },
}));

const BuyerOffersSteps = (props: Props): Element<'div' | typeof Fragment> => {
  const { offers, getBuyerOffers, isLoading } = props;
  const classes = useStyles();
  const [selectedOffer, setSelectedOffer] = useState(undefined);
  const [openOriginalOfferDialog, setOpenOriginalOfferDialog] = useState(false);
  const [openConfirmTransfer, setOpenConfirmTransfer] = useState(false);
  const [openScheduleInspection, setOpenScheduleInspection] = useState(false);
  const [openCreateEndpointAccount, setOpenCreateEndpointAccount] =
    useState(false);

  if (_isEmpty(offers) && !isLoading) {
    return <></>;
  }

  const openConfirmTransferHandler = offer => {
    setSelectedOffer(offer);
    setOpenConfirmTransfer(true);
  };

  const closeConfirmTransferHandler = offer => {
    setOpenConfirmTransfer(false);
    getBuyerOffers();
  };

  const openScheduleInspectionHandler = offer => {
    setSelectedOffer(offer);
    setOpenScheduleInspection(true);
  };

  const closeScheduleInspectionHandler = offer => {
    setOpenScheduleInspection(false);
    getBuyerOffers();
  };

  const openCreateEndpointAccountHandler = offer => {
    setSelectedOffer(offer);
    setOpenCreateEndpointAccount(true);
  };

  const closeCreateEndpointAccountHandler = () => {
    setOpenCreateEndpointAccount(false);
    getBuyerOffers();
  };

  const openOriginalOfferDialogHandler = offer => {
    setSelectedOffer(offer);
    setOpenOriginalOfferDialog(true);
  };

  const mappingSelectedOffer =
    selectedOffer && mappingCounterData(selectedOffer);

  return (
    <BlockUi
      tag="div"
      blocking={isLoading}
      loader={<Loader active type="ball-pulse" color="rgba(231,28,99,1)" />}
      className={clsx(classes.container, {
        [classes.loading]: isLoading,
      })}
    >
      {offers.map(offer => (
        <Offer
          offer={offer}
          key={_get(offer, 'OfferId')}
          openConfirmTransferHandler={openConfirmTransferHandler}
          openScheduleInspectionHandler={openScheduleInspectionHandler}
          openOriginalOfferDialogHandler={openOriginalOfferDialogHandler}
          openCreateEndpointAccountHandler={openCreateEndpointAccountHandler}
        />
      ))}
      <ConfirmTransferModal
        open={openConfirmTransfer}
        handleClose={closeConfirmTransferHandler}
        offer={mappingSelectedOffer}
        hideConfirmButton={hideWireConfirmButton(selectedOffer)}
      />
      <ConfirmInspectionDialog
        open={openScheduleInspection}
        handleClose={closeScheduleInspectionHandler}
        offer={mappingSelectedOffer}
      />
      <CreateEndpointAccount
        open={openCreateEndpointAccount}
        handleClose={closeCreateEndpointAccountHandler}
        offer={mappingSelectedOffer}
        hideConfirmButton={hideCreateEndPointAccountConfirmButton(
          selectedOffer,
        )}
      />
      <OriginalOfferDialog
        open={openOriginalOfferDialog}
        onClose={() => setOpenOriginalOfferDialog(false)}
        offer={mappingSelectedOffer}
      />
    </BlockUi>
  );
};

export default (withErrorBoundary(
  BuyerOffersSteps,
): Element<PropsWithoutRedux>);
