// @flow
import React, { memo, useEffect } from 'react';
import type { Element, AbstractComponent } from 'react';
import Typography from '@material-ui/core/Typography';
import { Divider, Button, Grid, makeStyles } from '@material-ui/core';
import {
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
} from 'components/Dialog';
import _get from 'lodash/get';
import { toastr } from 'react-redux-toastr';
import { OFFER_ERROR_MESSAGES } from 'shared/Messages';
import type { Offer } from 'types/Offer';
import { hideIfInvalidProps } from 'utils/RenderComponent';
import { useBuyerUpdateStep } from 'hooks';
import { compose } from 'redux';
import { OFFER_STEPS, CLOSING_ENDPOINT } from 'shared/Consts';

type Props = {
  offer: Offer | typeof undefined,
  open: boolean,
  handleClose: Function,
  hideConfirmButton?: boolean,
};

const useStyles = makeStyles(theme => ({
  spacing: {
    padding: theme.spacing(2),
  },
  title: {
    fontFamily: theme.typography.fontFamily.avenir85,
  },
  dialogContent: {
    fontFamily: theme.typography.fontFamily.avenir85,
    padding: theme.spacing(2),
    paddingTop: 0,
  },
  actionWrapper: {
    backgroundColor: theme.palette.background.lightGray,
    justifyContent: 'flex-end',
    padding: '1rem',
  },
  acceptButton: {
    borderRadius: 0,
    padding: '0.5625rem 2rem',
    color: theme.palette.common.white,
    borderWidth: '2px',
  },
  openLinkButton: {
    marginTop: '10px',
  },
  declineButton: {
    borderRadius: 0,
    padding: '0.5625rem 2rem',
    color: theme.palette.error.dark,
  },
}));

const checkProps = ({ offer }) => offer && _get(offer, ['OfferId']);

const CreateEndpointAccount = ({
  open,
  handleClose,
  offer,
  hideConfirmButton = false,
}: Props): Element<typeof Dialog> => {
  const classes = useStyles();
  const { doBuyerUpdateStep, isBuyerUpdatingStep, error } =
    useBuyerUpdateStep();

  const onClickConfirm = async () => {
    await doBuyerUpdateStep({
      variables: {
        OfferId: _get(offer, ['OfferId']),
        NewStepId: OFFER_STEPS.confirmTransfer,
      },
    });
    handleClose();
  };

  const onClickOpenNewLink = () => {
    window.open(CLOSING_ENDPOINT, '_blank');
  };

  useEffect(() => {
    const { message } = error || {};

    if (message) {
      toastr.error(OFFER_ERROR_MESSAGES.saveFailure, message);
    }
  }, [error]);

  return (
    <Dialog
      open={open}
      onClose={handleClose}
      scroll="paper"
      fullWidth
      maxWidth="sm"
      visible={1}
    >
      <DialogTitle
        id="customized-dialog-title"
        disableTypography
        onClose={handleClose}
        classes={{ root: classes.spacing }}
      >
        <Typography variant="h3" className={classes.title}>
          Create Endpoint Account
        </Typography>
      </DialogTitle>
      <DialogContent className={classes.dialogContent}>
        <Typography variant="h4">
          Please click the button below to create your endpoint account. Once
          you have created your account and confirmed the Buyer has created an
          account, click the &rdquo;Confirm Account Creation button.&rdquo;
          <br />
          <button
            className={`${classes.openLinkButton} prev_btn`}
            onClick={onClickOpenNewLink}
          >
            Create Account
          </button>
        </Typography>
      </DialogContent>
      <Divider />
      {!hideConfirmButton && (
        <DialogActions>
          <Grid container alignItems="center" className={classes.actionWrapper}>
            <Button
              variant="contained"
              color="primary"
              disableElevation
              className={classes.acceptButton}
              onClick={onClickConfirm}
              disabled={isBuyerUpdatingStep}
            >
              Confirm Account Creation
            </Button>
          </Grid>
        </DialogActions>
      )}
    </Dialog>
  );
};

export default (compose(
  hideIfInvalidProps(checkProps),
  memo,
)(CreateEndpointAccount): AbstractComponent<Props>);
